<template>
    <div class="sh-upload">
      <input type="file" class="sh-upload__input" ref="file1" name="filename" @change="handleChange($event)" :accept="accept">
      <div @click="updatefile">
        <slot v-if=""></slot>
        <template v-if="!$slots.default">
          <button>updateFile</button>
        </template>
      </div>
    </div>
</template>

<script>

export default {
  name: 'ShUpdate',
  props:{
    accept:{
      type: String,
      default: ''
    },
    value: Array,//
    beforeUpload: Function,//
    limit: Number,//
    fileList: Array,//
    onExceed: Function,//
  },
  methods: {
    handleChange(ev){
      const files = ev.target.files;
      if (!files) return;
      this.uploadFiles(files);
    },
    uploadFiles(files) {
      //
      if (files.length > this.limit) {
        this.onExceed && this.onExceed(files, this.fileList);
        return;
      }
      //
      if (this.beforeUpload) {  //
        for(var i=0;i<files.length;i++){
          this.beforeUpload(files[i]);
        }
      }
      //
      let postFiles = Array.prototype.slice.call(files);
      if(this.value){
        this.$emit('input',postFiles)
      }
    },
    updatefile(){
      this.$refs.file1.click()
    }
  }
}
</script>

<style lang="scss" scoped>
@charset "UTF-8";
.sh-upload {
	display:inline-block;
	text-align:center;
	cursor:pointer;
	outline:0;
  >.sh-upload__input {
    display:none
  }
}
</style>



