<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Checkbox </h2>
            <p slot="title" ></p>
        </Viwe>
        <Viwe>
            <h3 slot="name" ></h3>
            <p slot="title" > checkbox </p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <template>
                            <sh-checkbox v-model="checked">1</sh-checkbox>
                            <sh-checkbox v-model="checked">2</sh-checkbox>
                        </template>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>el-checkboxv-modelcheckboxBooleantrue</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;template&gt;
  &lt;!-- &#x60;checked&#x60;  true  false --&gt;
  &lt;sh-checkbox v-model=&quot;checked&quot;&gt;&lt;/sh-checkbox&gt;
&lt;/template&gt;
&lt;script&gt;
  export default {
    data() {
      return {
        checked: true
      };
    }
  };
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'':'' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" ></h3>
            <p slot="title" ></p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <template>
                            <sh-checkbox v-model="checked1" disabled>1</sh-checkbox>
                            <sh-checkbox v-model="checked2" disabled></sh-checkbox>
                        </template>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive1">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>disabled</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;template&gt;
  &lt;sh-checkbox v-model=&quot;checked1&quot; disabled&gt;1&lt;/sh-checkbox&gt;
  &lt;sh-checkbox v-model=&quot;checked2&quot; disabled&gt;&lt;/sh-checkbox&gt;
&lt;/template&gt;
&lt;script&gt;
  export default {
    data() {
      return {
        checked1: false,
        checked2: true
      };
    }
  };
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive1" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive1" class="sh-icon-caret-top"></i>
                        <span @click="isActive1 = !isActive1">{{ isActive1?'':'' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Checkbox',
  data(){
    return {
        isActive: false,
        isActive1: false,
        checked: true,
        checked1: true,
        checked2: true
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



