<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Upload </h2>
            <p slot="title" ></p>
        </Viwe>
        <Viwe :explain="explain">
            <h3 slot="name" ></h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <sh-update 
                          :before-upload="fun1"
                          v-model="fileData"
                          >
                          <ShButton size="small" type="primary"></ShButton>{{ filename }}
                        </sh-update>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description tip">
                                    <p> slot limiton-exceedbefore-remove</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;sh-update 
  :before-upload=&quot;fun1&quot;
  v-model=&quot;fileData&quot;
  &gt;
  &lt;ShButton size=&quot;small&quot; type=&quot;primary&quot;&gt;&lt;/ShButton&gt;{{ filename }}
&lt;/sh-update&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'':'' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Update',
  data(){
    return {
        isActive: false,
        dialogVisible: false,
        filename: '',
        fileData: [],
        explain: [
            {
                "name": "Update Attributes",
                "title":['','','','',''],
                "value":[
                    ['v-model','','array','','[]'],
                    ['accept','thumbnail-mode ','string','',''],
                    ['limit','','number','',''],
                    ['file-list',', : [file,file,...]','array','','[]'],
                    ['on-exceed','','function(files,fileList)','','-'],
                    ['before-upload','','function(file)','','']
                ],
            }
        ]
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
    fun1(flie){
        this.filename = flie.name
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



