<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Radio </h2>
            <p slot="title" ></p>
        </Viwe>
        <Viwe>
            <h3 slot="name" ></h3>
            <p slot="title" > Select </p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <template>
                            <sh-radio v-model="radio" label="1"></sh-radio>
                            <sh-radio v-model="radio" label="2"></sh-radio>
                        </template>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p> Radio v-model Radio labellabelStringNumberBoolean</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;template&gt;
  &lt;sh-radio v-model=&quot;radio&quot; label=&quot;1&quot;&gt;&lt;/sh-radio&gt;
  &lt;sh-radio v-model=&quot;radio&quot; label=&quot;2&quot;&gt;&lt;/sh-radio&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        radio: &#x27;1&#x27;
      };
    }
  }
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'':'' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" ></h3>
            <p slot="title" ></p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <template>
                            <sh-radio disabled v-model="radio" label=""></sh-radio>
                            <sh-radio disabled v-model="radio" label=""></sh-radio>
                        </template>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive1">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>el-radiodisabledBooleantrue</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;template&gt;
  &lt;sh-radio disabled v-model=&quot;radio&quot; label=&quot;&quot;&gt;&lt;/sh-radio&gt;
  &lt;sh-radio disabled v-model=&quot;radio&quot; label=&quot;&quot;&gt;&lt;/sh-radio&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        radio: &#x27;&#x27;
      };
    }
  }
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive1" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive1" class="sh-icon-caret-top"></i>
                        <span @click="isActive1 = !isActive1">{{ isActive1?'':'' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Radio',
  data(){
    return {
        isActive: false,
        isActive1: false,
        radio: '1',
        resource: ''
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



