<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Container </h2>
            <p slot="title" >
<br/>
&lt;el-container&gt; &lt;el-header&gt;  &lt;el-footer&gt; 
<br/>
&lt;el-header&gt;
<br/>
&lt;el-aside&gt;
<br/>
&lt;el-main&gt;
<br/>
&lt;el-footer&gt;
<br/>
</p>
<div class="tip"> flex &lt;el-container&gt;  &lt;el-container&gt;</div>
        </Viwe>
        <Viwe :explain="explain">
            <h3 slot="name" ></h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <sh-container>
                            <sh-header>Header</sh-header>
                            <sh-main>Main</sh-main>
                        </sh-container>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;el-container&gt;
  &lt;el-header&gt;Header&lt;/el-header&gt;
  &lt;el-main&gt;Main&lt;/el-main&gt;
&lt;/el-container&gt;

&lt;el-container&gt;
  &lt;el-header&gt;Header&lt;/el-header&gt;
  &lt;el-main&gt;Main&lt;/el-main&gt;
  &lt;el-footer&gt;Footer&lt;/el-footer&gt;
&lt;/el-container&gt;

&lt;el-container&gt;
  &lt;el-aside width=&quot;200px&quot;&gt;Aside&lt;/el-aside&gt;
  &lt;el-main&gt;Main&lt;/el-main&gt;
&lt;/el-container&gt;

&lt;el-container&gt;
  &lt;el-header&gt;Header&lt;/el-header&gt;
  &lt;el-container&gt;
    &lt;el-aside width=&quot;200px&quot;&gt;Aside&lt;/el-aside&gt;
    &lt;el-main&gt;Main&lt;/el-main&gt;
  &lt;/el-container&gt;
&lt;/el-container&gt;

&lt;el-container&gt;
  &lt;el-header&gt;Header&lt;/el-header&gt;
  &lt;el-container&gt;
    &lt;el-aside width=&quot;200px&quot;&gt;Aside&lt;/el-aside&gt;
    &lt;el-container&gt;
      &lt;el-main&gt;Main&lt;/el-main&gt;
      &lt;el-footer&gt;Footer&lt;/el-footer&gt;
    &lt;/el-container&gt;
  &lt;/el-container&gt;
&lt;/el-container&gt;

&lt;el-container&gt;
  &lt;el-aside width=&quot;200px&quot;&gt;Aside&lt;/el-aside&gt;
  &lt;el-container&gt;
    &lt;el-header&gt;Header&lt;/el-header&gt;
    &lt;el-main&gt;Main&lt;/el-main&gt;
  &lt;/el-container&gt;
&lt;/el-container&gt;

&lt;el-container&gt;
  &lt;el-aside width=&quot;200px&quot;&gt;Aside&lt;/el-aside&gt;
  &lt;el-container&gt;
    &lt;el-header&gt;Header&lt;/el-header&gt;
    &lt;el-main&gt;Main&lt;/el-main&gt;
    &lt;el-footer&gt;Footer&lt;/el-footer&gt;
  &lt;/el-container&gt;
&lt;/el-container&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'':'' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Container',
  data(){
    return {
        isActive: false,
        explain: [
            {
                "name": "Container Attributes",
                "title":['','','','',''],
                "value":[
                    ['direction','','string','horizontal / vertical',' el-header  el-footer  vertical horizontal'],
                ],
            },
            {
                "name": "Header Attributes",
                "title":['','','','',''],
                "value":[
                    ['height','','string','','60px']
                ],
            },
            {
                "name": "Aside Attributes",
                "title":['','','','',''],
                "value":[
                    ['width','','string','','300px']
                ],
            },
            {
                "name": "Footer Attributes",
                "title":['','','','',''],
                "value":[
                    ['height','','string','','60px']
                ],
            }
        ]
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



